@use 'variables' as v;
@use 'mixins' as m;

h1, h2, h3 {
    @include m.headings;
}

h1 {
    font-size: 4rem;
}

h2 {
    font-size: 3.6rem;
}

h3 {
    font-size: 3rem;
}

.text-center {
    text-align: center;
}

// This means that it's a class that ends with __heading, there are others to check in the documentation of mdn web docs

[class$="__heading"] {
    @include m.page-name;
}

[class$="__description"] {
    @include m.page-description;
}